body {
  padding: 0;
  margin: 0;
  background-color: #f2ceaf;
  font-family: "Open Sans", sans-serif;
}

h1 {
  font-family: "Yesteryear", cursive;
  font-size: 40px;
  text-align: center;
  text-transform: capitalize;
  color: #3e424e;
}
.container {
  display: flex;
  flex-direction: row;
  cursor: pointer;
  flex-wrap: wrap;
}
.sidebar {
  width: 20%;
  text-align: center;
  background-color: #3e424e;
  height: 100%;
}
.sidebar h4 {
  text-transform: uppercase;
  color: #f2ceaf;
  letter-spacing: 1px;
}
.sidebar img {
  width: 100%;
  object-fit: contain;
  letter-spacing: 1px;
}
.main {
  width: 75%;
  display: flex;
  justify-items: center;
  flex-direction: column;
  align-items: center;
  cursor: auto;
}
textarea {
  width: 640px;
  height: 100px;
  margin-top: 30px;
}
.colorPicker {
  margin-top: 30px;
}
.colorPicker label {
  font-weight: 600;
}
.downloadIcon {
  position: fixed;
  top: 100px;
  right: 50px;
  height: 50px;
  width: 50px;
  object-fit: contain;
  cursor: pointer;
}
